<template>
    <v-app-bar dark :clipped-left="$vuetify.breakpoint.lgAndUp"
               app
               color="green accent-4"
               dense>

        <v-app-bar-nav-icon v-show="btnLeftPanelShow" @click.stop="closeOrOpenLeftPanel()"></v-app-bar-nav-icon>

        <v-toolbar-title class="ml-0 pl-4 pr-7" style="cursor: pointer" @click="goto('/')">
            <span class="hidden-sm-and-down">SmartGeo</span>
        </v-toolbar-title>

        <span class="hidden-sm-and-down pt-1" >{{date | date('datetime')}}</span>

        <v-spacer></v-spacer>
        <v-spacer></v-spacer>

        <v-text-field dark flat solo-inverted
                      hide-details
                      dense
                      prepend-inner-icon="mdi-magnify"
                      :label="$t('message.search')"
                      class="hidden-sm-and-down"
                      style="display: block!important;"
                      v-model="searchText">
        </v-text-field>

        <v-spacer></v-spacer>
        <v-spacer></v-spacer>

        <v-menu offset-y class="">
            <template v-slot:activator="{ on }">
                <v-btn class="px-0 mr-3" min-width="40" color="green "
                       dark
                       v-on="on">
                    {{$i18n.locale}}
                </v-btn>
            </template>
            <v-list dense class="ma-0 pa-0" min-width="40">
                <v-list-item v-for="(lang, index) in langs"
                             :key="index"
                             @click="changeLang(lang)"
                             class="px-3">
                    <v-list-item-title class="text-uppercase">{{ lang }}</v-list-item-title>
                </v-list-item>
            </v-list>
        </v-menu>

        <v-menu offset-y>
            <template v-slot:activator="{ on }">

                <v-btn icon v-on="on" class="ma-0">

                    <v-avatar width="40px" height="40px">
                        <img :src="userPhotoUrl ? userPhotoUrl : defaultAvatarUrl" alt="Avatar" class="avatar">
                    </v-avatar>
                </v-btn>
            </template>
            <v-list>
                <v-list-item>
                    <v-list-item-avatar>
                        <img :src="userPhotoUrl ? userPhotoUrl : defaultAvatarUrl" alt="Avatar" class="avatar">
                    </v-list-item-avatar>
                    <v-list-item-content>
                        <v-list-item-title>{{userFullName ? userFullName : 'test testov'}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
            <v-divider></v-divider>
            <v-list>
                <v-list-item v-for="(item, i) in header_menu"
                             :key="i" @click="goto(item.link)">
                    <v-list-item-icon>
                        <v-icon v-text="item.icon"></v-icon>
                    </v-list-item-icon>
                    <v-list-item-title v-text="item.name">
                    </v-list-item-title>
                </v-list-item>
            </v-list>
        </v-menu>
    </v-app-bar>
</template>

<script>
    export default {
        name: "Header",
        props: {
            btnLeftPanelShow: Boolean
        },
        data() {
            return {
                langs: ['en', 'ru', 'uz'],
                curr_lang: 'en',
                searchText: '',
                defaultAvatarUrl: 'https://www.w3schools.com/howto/img_avatar.png',
                offset: true,
                date: new Date(),
                interval: null,
                dropdown: null,
                header_menu: [
                    {name: 'Профиль', link: '/profile', icon: 'account_circle'},
                    {name: 'О программе', link: '/about', icon: 'info'},
                    {name: 'Выйти', link: '/logout', icon: 'assignment_return'}
                ],
                userFullName: (this.$store.getters.getExtUser.first_name || '') + ' ' +
                    (this.$store.getters.getExtUser.last_name || ''),
                userPhotoUrl: this.$store.getters.getExtUser.photo_url || ''
            }
        },
        mounted() {
            this.$i18n.locale = localStorage.getItem('cur_lang') || 'en'
            this.interval = setInterval(() => {
                this.date = new Date()
            }, 1000)
        },
        methods: {
            changeLang(lang) {
                localStorage.setItem('cur_lang', lang)
                this.$i18n.locale = lang
                this.$sayHello('Hello')
            },
            async logout() {
                this.$store.dispatch('LOGOUT').then(() => {
                    this.$router.push('/login?message=logout')
                });
            },
            goto(path) {
                if (path !== '/logout') {
                    this.$router.push(path)
                } else {
                    this.logout();
                }
            },
            closeOrOpenLeftPanel() {
                console.log(this.$store.getters.getIsLeftPanelOpen)
                this.$store.dispatch('SET_IS_LEFT_PANEL_OPEN', !this.$store.getters.getIsLeftPanelOpen)
            }
        },
        computed: {}
    }
</script>

<style>
    div.search-list {
        position: absolute;
        left: 40%;
        top: 45px;
        max-width: 506px;
    }

    @media only screen and (max-width: 959px) {
        div.search-list {
            position: absolute;
            left: 35%;
            top: 45px;
            max-width: 350px;
        }
    }

    @media only screen and (max-width: 650px) {
        div.search-list {
            position: absolute;
            left: 25%;
            top: 45px;
            max-width: 350px;
        }
    }
</style>