<template>
    <v-container>
        <v-form>
            <v-col cols="12" md="12">
                <v-text-field label="Search events"
                              outlined
                              dense
                              class="ma-0 pa-0">
                </v-text-field>
            </v-col>
            <v-row class="px-2 pr-3">
                <v-col cols="12" md="6" class="pa-0 pl-4 pr-1 ma-0">
                    <v-menu v-model="startDateMenu"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="scale-transition"
                            offset-y
                            min-width="290px">
                        <template v-slot:activator="{ on }">
                            <v-text-field v-model="dateStart"
                                          :label="$t('message.start')"
                                          prepend-icon="event"
                                          readonly
                                          dense
                                          v-on="on">
                            </v-text-field>
                        </template>
                        <v-date-picker v-model="dateStart" locale="ru" @input="startDateMenu= false"
                                       no-title></v-date-picker>
                    </v-menu>
                </v-col>
                <v-col cols="12" md="6" class="pa-0 pr-3 pl-2 ma-0">
                    <v-menu v-model="endDateMenu"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="scale-transition"
                            offset-y
                            min-width="290px">
                        <template v-slot:activator="{ on }">
                            <v-text-field v-model="dateEnd"
                                          :label="$t('message.end')"
                                          prepend-icon="event"
                                          readonly
                                          dense
                                          v-on="on">
                            </v-text-field>
                        </template>
                        <v-date-picker v-model="dateEnd" @input="endDateMenu= false" no-title></v-date-picker>
                    </v-menu>
                </v-col>
            </v-row>
            <v-row justify="start">
                <v-col cols="12" md="7" class="pa-0 pl-4 pr-1 ma-0">
                    <v-checkbox v-model="activeEvent" class="mx-2 my-0" label="Active only"></v-checkbox>
                    <v-checkbox v-model="ownEvent" class="mx-2 my-0" label="My own events only"></v-checkbox>
                    <v-checkbox v-model="moderatorEvent" class="mx-2 my-0" color="red" label="For moderation"></v-checkbox>
                </v-col>
              <!--  <v-col cols="12" md="7" class="pa-0 pl-4 pr-1 ma-0">

                </v-col>
                <v-col cols="12" md="6" class="pa-0 pl-4 pr-1 ma-0">

                </v-col>-->
                <v-col cols="12" md="5" class="pa-0 pl-4 pr-1 ma-0">
                    <v-spacer></v-spacer>
                    <v-btn color="success" @click="startEddEvent()"
                           class="ma-2">
                        {{ $t("message.add") }}
                    </v-btn>
                </v-col>

            </v-row>
        </v-form>

        <v-divider></v-divider>
        <v-list dense>
            <v-subheader ><h3>Events</h3></v-subheader>
                <v-list-item
                        v-for="(event, i) in events"
                        :key="i"
                >
                    <v-list-item-icon>
                        <v-icon v-text="event.icon"></v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                        <v-list-item-title v-text="event.text"></v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
        </v-list>
    </v-container>
</template>

<script>
    import {CONSTANTS} from "../../utils/constants";

    export default {
        name: "EventList",
        data: () => ({
            activeEvent: false,
            ownEvent: false,
            moderatorEvent: false,
            dateStart: new Date().toISOString().substr(0, 10),
            dateEnd: new Date().toISOString().substr(0, 10),
            startDateMenu: false,
            endDateMenu: false,
            events: [
                { text: 'Real-Time', icon: 'mdi-clock' },
                { text: 'Audience', icon: 'mdi-account' },
                { text: 'Conversions', icon: 'mdi-flag' }
            ],
        }),
        mounted() {
           // this.$sayHello('Hello to')
           //  console.log('event list')
        },
        methods: {
            startEddEvent() {
                // console.log('CURRENT APP state: ' + this.$store.getters.getCurrentState)
                this.$store.dispatch("STATE_CHANGED", CONSTANTS.APP_STATE.ADD_EVENT)
            },
            cancelAdding() {
                // console.log('CURRENT APP state: ' + this.$store.getters.getCurrentState)
                this.$store.dispatch("STATE_CHANGED", CONSTANTS.APP_STATE.CANCEL_ADD_EVENT)

            },
        },
    }
</script>

<style scoped>

</style>