import axios from "axios";
import {CONSTANTS} from "../utils/constants";

export default {
    state: {
        geoJsonEditedPointFeature: null,
        newAddedEvent: null,
        eventsNeedHelp: null,
        eventsReadyAssist: null,
        eventsWater: null,
        eventsFood: null
    },
    getters: {
        getGeoJsonEditedPointFeature(state) {
            return state.geoJsonEditedPointFeature
        },
        getNeedHelpEvents(state) {
            return state.eventsNeedHelp
        },
        getReadyAssistEvents(state) {
            return state.eventsReadyAssist
        },
        getWaterEvents(state) {
            return state.eventsWater
        },
        getFoodEvents(state) {
            return state.eventsFood
        }
    },
    actions: {
        GET_EVENTS_BY_CATEGORY(context, categoryId) {
            // console.log('ACTION GET_EVENTS_BY_CATEGORY STARTED')
            try {

                let postParams = JSON.stringify({
                    user_id: 0,
                    category_id: categoryId
                })

                if (!axios.defaults.headers.common['Authorization']) {
                    // console.log('EMPTY AUTH HEADER DATA')
                    axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.getters.getExtUser.hash
                }

                // console.log(postParams)

                axios.post('event/protected/getEvents', postParams)
                    .then(res => {
                        // console.log(res.data.result)
                        if (res.data.result) {
                            let payload = {'events': res.data.result, 'categoryId': categoryId}

                            switch (categoryId) {
                                case CONSTANTS.EVENT_CATEGORY.NEED_HELP:
                                    context.commit('SET_EVENTS_BY_CATEGORY', payload)
                                    break;
                                case CONSTANTS.EVENT_CATEGORY.READY_ASSIST:
                                    context.commit('SET_EVENTS_BY_CATEGORY', payload)
                                    break;
                                case CONSTANTS.EVENT_CATEGORY.WATER:
                                    context.commit('SET_EVENTS_BY_CATEGORY', payload)
                                    break;
                                case CONSTANTS.EVENT_CATEGORY.FOOD:
                                    context.commit('SET_EVENTS_BY_CATEGORY', payload)
                                    break;
                            }
                        } else {
                            console.log(res.data.error)
                            context.commit('SET_ERROR', res.data.error)
                        }
                    })
                    .catch(e => {
                        context.commit('SET_ERROR', e)
                        console.log('error- ' + e)
                    })
            } catch (e) {
                console.log(e)
                context.commit('SET_ERROR', e)
                throw e
            }
        },

        SET_GEOJSON_EDITED_POINT_FEATURE(context, value) {
            // console.log('ACTION STATE_CHANGED: ' + value)

            if (context.getters.getGeoJsonEditedPointFeature !== value) {
                // console.log('SET_GEOJSON_EDITED_POINT_FEATURE')
                context.commit('SET_GEOJSON_EDITED_POINT_FEATURE', value)
            } else {
                console.log('geoJsonEditedPointFeature ARE SAME!!!!!!')
            }
        },
        CLEAR_GEOJSON_EDITED_POINT_FEATURE(context) {
            // console.log('CLEAR_GEOJSON_EDITED_POINT_FEATURE')
            context.commit('CLEAR_GEOJSON_EDITED_POINT_FEATURE')
        },
        ADD_EVENT(context, newEvent) {
            // console.log('ACTION ADD_EVENT')
            // console.log(newEvent)

            // let payload = {'events': newEvent, 'categoryId': newEvent.categoryId}
            let events

            switch (newEvent.categoryId) {
                case CONSTANTS.EVENT_CATEGORY.NEED_HELP:
                    events = context.getters.getNeedHelpEvents
                    events.push(newEvent)
                    break;
                case CONSTANTS.EVENT_CATEGORY.READY_ASSIST:
                    events = context.getters.getReadyAssistEvents
                    events.push(newEvent)
                    break;
                case CONSTANTS.EVENT_CATEGORY.WATER:
                    events = context.getters.getWaterEvents
                    events.push(newEvent)
                    break;
                case CONSTANTS.EVENT_CATEGORY.FOOD:
                    events = context.getters.getFoodEvents
                    events.push(newEvent)
                    break;
            }
            context.commit('UPDATE_EVENTS', events)
            context.commit('ADD_EVENT', newEvent)
        },
    },
    mutations: {
        SET_GEOJSON_EDITED_POINT_FEATURE(state, value) {
            // console.log('MUTATION STATE_CHANGED: ' + value)
            state.geoJsonEditedPointFeature = value
        },
        CLEAR_GEOJSON_EDITED_POINT_FEATURE(state) {
            // console.log('CLEAR_GEOJSON_EDITED_POINT_FEATURE')
            state.geoJsonEditedPointFeature = null
        },
        SET_EVENTS_BY_CATEGORY(state, payload) {
            // console.log('SET_EVENTS_BY_CATEGORY')

            let events = payload.events
            let categoryId = payload.categoryId
            // console.log(events)
            switch (categoryId) {
                case CONSTANTS.EVENT_CATEGORY.NEED_HELP:
                    state.eventsNeedHelp = events
                    break;
                case CONSTANTS.EVENT_CATEGORY.READY_ASSIST:
                    state.eventsReadyAssist = events
                    break;

                case CONSTANTS.EVENT_CATEGORY.WATER:
                    state.eventsWater = events
                    break;

                case CONSTANTS.EVENT_CATEGORY.FOOD:
                    state.eventsFood = events
                    break;
            }
        },
        UPDATE_EVENTS(state, events) {
            // console.log('MUTATION STATE_CHANGED: ' + value)
            state.eventsNeedHelp = events
        },
        ADD_EVENT(state, newEvent) {
            // console.log('MUTATION STATE_CHANGED: ' + value)
            state.newAddedEvent = newEvent
        },
    }
}