const routes = [
    {
        path: '/login',
        name: 'login',
        meta: {
            layout: 'empty',
            withoutAuth: true
        },
        component: () => import('../views/Login.vue')
    },
    {
        path: '/register',
        name: 'register',
        meta: {
            layout: 'empty',
            requiresAuth: false,
            withoutAuth: true
        },
        component: () => import('../views/Register.vue')
    },
    {
        path: '/',
        name: 'home',
        meta: {
            layout: 'map',
            requiresAuth: true
        },
        component: () => import('../views/Home.vue')
    },
    {
        path: '/map',
        name: 'Map',
        meta: {
            layout: 'map',
            requiresAuth: true
        },
        component: () => import('../views/Map.vue')
    },
    {
        path: '/about',
        name: 'about',
        meta: {
            layout: 'main',
            withoutAuth: false
        },
        component: () => import('../views/About.vue')
    },
    {
        path: '/profile',
        name: 'profile',
        meta: {
            layout: 'main',
            requiresAuth: true
        },
        component: () => import('../views/Profile.vue')
    }
]

export default routes