import VueI18n from 'vue-i18n'

import VueToast from 'vue-toast-notification';
import 'vue-toast-notification/dist/theme-default.css';


import './assets/popup/ol-popup.css';

import commonLanguage from './lang/common-language'

import axios from 'axios'

import vuetify from './plugins/vuetify';

require("flatpickr/dist/themes/material_green.css");

const flatpickr = require("flatpickr");

// axios.defaults.baseURL = 'http://todo-laravel.test/api'
axios.defaults.baseURL = '//192.168.1.13:8083/api/'
// axios.defaults.baseURL = '//192.168.1.13:8779/'

Vue.use(VueI18n)
const messages = commonLanguage

// Create VueI18n instance with options
const i18n = new VueI18n({
    locale: 'en', // set locale
    messages, // set locale messages
})

// eslint-disable-next-line no-unused-vars
Vue.use(flatpickr)
Vue.use(Vuelidate)
Vue.use(messagePlugin)
Vue.use(VueGeolocation);
Vue.use(VueToast, {
    position: 'bottom-right'
});
Vue.filter('date', dateFilter)

Vue.use(VueRouter)

const router = new VueRouter({
    routes
    // mode: 'history'
})


