<template>
    <div>

        <cus-header  :btn-left-panel-show="false"></cus-header>

        <left-panel></left-panel>


        <v-content class="app-content">
            <div class="app-page">
                <router-view/>
            </div>
        </v-content>

    </div>
</template>

<script>
    import CusHeader from '../components/app/Header.vue'
    // eslint-disable-next-line no-unused-vars
    export default {
        name: 'main-layout',
        data() {
            return {
            }
        },
        methods: {

        },
        mounted() {
            this.interval = setInterval(() => {
                this.date = new Date()
            }, 1000)

        },

        beforeDestroy() {
            // console.log('beforeDestroy')
            clearInterval(this.interval)
            if (this.dropdown && this.dropdown.destroy) {
                this.dropdown.destroy()
            }
        },
        components: {
            CusHeader
        },
        computed: {
            drawer() {
                return this.$store.getters.getIsLeftPanelOpen
            },
            isTyping() {
                return this.searchText.length > 0 ? 'display: block' : 'display: none'
            },
            error() {
                return this.$store.getters.error
            }
        },
        watch: {
            error(error) {
                // console.log(error)
                // this.$error(messages[error.code] || 'Something went wrong')
                this.$error(error)
            }
        }
    }
</script>
