<template>
  <v-container>
    <v-toolbar flat>
      <v-toolbar-title class="font-weight-light">Add event on a map</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn fab
             small
             @click="cancelAdding()">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-toolbar>
    <form class="pa-3">
      <v-text-field v-model="searchAddress"
                    label="Search Address"
                    class="search-address"
                    outlined
                    dense
                    required>
      </v-text-field>
      <v-row class="ma-0">
        <v-col class="pa-0">
          <h4>Lat / Long: </h4>
          <v-label>{{'43,090456323 / 34,09823098123'}}</v-label>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <h4>Address: </h4>
          <v-label>{{event.address}}</v-label>
        </v-col>
      </v-row>
      <v-select v-model="event.category"
                :items="categories"
                label="Select"
                data-vv-name="select"
                outlined
                dense
                required>
      </v-select>
      <v-autocomplete
              v-model="event.category"
              :items="categories"
              filled
              outlined
              dense
              color="blue-grey lighten-2"
              label="Select"
              item-text="name"
              item-value="id"
      >
        <template v-slot:selection="data">
          <v-chip
                  v-bind="data.attrs"
                  :input-value="data.selected"
                  @click="data.select"
                  @click:close="remove(data.item)"
          >
            <v-avatar left>
              <v-img :src="data.item.pin_url"></v-img>
            </v-avatar>
            {{ data.item.name }}
          </v-chip>
        </template>
        <template v-slot:item="data">
          <template v-if="typeof data.item !== 'object'">
            <v-list-item-content v-text="data.item"></v-list-item-content>
          </template>
          <template v-else>
            <v-list-item-avatar>
              <img :src="data.item.pin_url">
            </v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title v-html="data.item.name"></v-list-item-title>
            </v-list-item-content>
          </template>
        </template>
      </v-autocomplete>
      <v-text-field
              v-model="event.name"
              label="Event topic"
              outlined
              dense
              required>
      </v-text-field>
      <v-textarea
              name="input-5-1"
              label="Event description"
              v-model="event.description"
              outlined
              dense
              hint="Hint text"
      ></v-textarea>
      <v-checkbox
              v-model="event.active"
              value="1"
              label="Active"
              type="checkbox"
              required
      ></v-checkbox>
      <v-divider></v-divider>


      <v-spacer></v-spacer>


      <v-btn class="pa-2 mr-3" @click="clear">{{ $t("message.clear") }}</v-btn>
      <!--            <v-btn class="ma-3 pa-2" @click="cancelAdding"> {{ $t("message.cancel") }}</v-btn>-->

      <v-btn color="primary" @click="submit()"
             :disabled="isValid"
             class=" pa-2 white--text">
        {{ $t("message.save") }}
      </v-btn>
    </form>

  </v-container>
