                          label="Active"
                          type="checkbox"
                          required
                  ></v-checkbox>
              </v-card-text>

              <v-divider></v-divider>
              <v-card-actions>
                  <v-spacer></v-spacer>

                  <v-btn class="pa-2 mx-3" @click="clear">{{ $t("message.clear") }}</v-btn>
&lt;!&ndash;                    <v-btn class="mx-3 pa-2" @click="cancelAdding"> {{ $t("message.cancel") }}</v-btn>&ndash;&gt;

                  <v-btn color="primary" @click="submit()"
                         :disabled="isValid"
