</template>

<script>
    import {CONSTANTS} from "../../utils/constants";
    import axios from "axios";

    export default {
        name: "AddEvent",
        data() {
            const srcs = {
                1: require('../../assets/icons/sos.png'),
                2: require('../../assets/icons/small_assist.png')
            }
            return {
                hasSaved: false,
                isEditing: false,
                searchAddress: '',
                topic: '',
                event: {
                    user_id: null,
                    category: 1,
                    name: '',
                    description: '',
                    address: 'Tashkent, Uzbekistan',
                    geojson: null,
                    start_date: new Date().toISOString().substr(0, 10),
                    end_date: new Date().toISOString().substr(0, 10)
                },
                select: null,
                categories: [
                    {id: 1, name: 'Sandra Adams', pin_url: srcs[1]},
                    {id: 2, name: 'Ali Connors', pin_url: srcs[2]},
                ],
                isActive: null,
            }
        },
        methods: {
            cancelAdding() {
                // console.log('CURRENT APP state: ' + this.$store.getters.getCurrentState)
                this.$store.dispatch("STATE_CHANGED", CONSTANTS.APP_STATE.CANCEL_ADD_EVENT)
            },
            submit() {
                // this.isEditing = !this.isEditing
                this.hasSaved = true
                this.saveEvent()
                this.$toast.info('save')
            },
            saveEvent() {
                try {
                    // console.log(this.editedPointFeature)

                    if (!axios.defaults.headers.common['Authorization']) {
                        console.log('EMPTY AUTH HEADER DATA')
                        axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$store.getters.getExtUser.hash
                    }

                    if (this.$store.getters.getGeoJsonEditedPointFeature) {

                        console.log(this.event.category)

                        let postParams = JSON.stringify({
                            user_id: this.$store.getters.getDbUser.id,
                            category_id: this.event.category,
                            name: 'name text',
                            description: 'description text',
                            address: 'address text',
                            geojson: this.$store.getters.getGeoJsonEditedPointFeature,
                            start_date: new Date().toISOString().substr(0, 10),
                            end_date: new Date().toISOString().substr(0, 10)
                        })

                        let postParamsObject = JSON.parse(postParams)
                        console.log(postParamsObject)


                        axios.post('event/protected/save', postParams)
                            .then(res => {
                                // console.log(res.data)
                                if (res.data.result) {
                                    // console.log('EVENT SAVED TO DB ' + res.data.result.id)

                                    postParamsObject.id = res.data.result.id

                                    this.$store.dispatch('ADD_EVENT', postParamsObject)
                                    this.$store.dispatch('STATE_CHANGED', CONSTANTS.APP_STATE.DEFAULT)
                                } else {
                                    console.log(res.data.error)
                                    this.$store.dispatch('SET_ERROR', res.data.error)
                                }
                            })
                            .catch(e => {
                                this.$store.dispatch('SET_ERROR', e)
                                console.log('error- ' + e)
                            })
                    }

                } catch (e) {
                    console.log(e)
                    throw e
                }
            },
            clear() {
                this.searchAddress = ''
                this.topic = ''
                this.select = null
                this.isActive = null

                this.$store.dispatch("STATE_CHANGED", CONSTANTS.APP_STATE.ADD_EVENT)

                // this.$toast.info('cleared')
            },
        },
        computed: {
            isValid() {
                return this.event.active == null
                // this.searchAddress.length == 0 ||
                //        this.topic == '' ||
                //        this.select == null ||

            }
        }
    }

</script>

<style scoped>
  .search-address > .v-input__control > .v-text-field__details {
    display: none;
  }

  div.v-text-field .v-text-field--enclosed .v-text-field__details {
    margin: 0;
  }

</style>