<template>
  <v-navigation-drawer v-model="drawer"
                       width="400px"
                       :clipped="true"
                       mobile-break-point="860px"
                       app>
    <add-event v-show="isAddState"></add-event>
    <event-list v-show="isListState"></event-list>
  </v-navigation-drawer>

</template>

<script>
    import {CONSTANTS} from "../../utils/constants";
    import EventList from "../app/EventList.vue";
    import AddEvent from "../app/AddEvent.vue";


    export default {
        props: {
            isDrawer: Boolean
        },
        name: "LeftPanel",
        components: {
            EventList,
            AddEvent
        },
        data: () => ({

            isMobile: window.innerWidth < 900,
            activeEvent: false,
            ownEvent: false,
            moderatorEvent: false,
            dateStart: new Date().toISOString().substr(0, 10),
            dateEnd: new Date().toISOString().substr(0, 10),
            startDateMenu: false,
            endDateMenu: false,
            items: [
                {title: 'Login', url: '/login', exact: true},
                {title: 'Home', url: '/', exact: true},
                {title: 'Map', url: '/map', exact: true},
                {title: 'About', url: '/about', exact: true}
            ],
        }),
        beforeCreate() {
            this.$store.dispatch('GET_EVENTS_BY_CATEGORY', 1)
            this.$store.dispatch('GET_EVENTS_BY_CATEGORY', 2)
            this.$store.dispatch('GET_EVENTS_BY_CATEGORY', 3)
            this.$store.dispatch('GET_EVENTS_BY_CATEGORY', 4)
        },
        mounted() {
            let isMobile = window.innerWidth < 900
            if (isMobile) {
                this.$store.dispatch('SET_IS_LEFT_PANEL_OPEN', false)
            }
        },
        methods: {
            startEddEvent() {
                // console.log('CURRENT APP state: ' + this.$store.getters.getCurrentState)
                this.$store.dispatch("STATE_CHANGED", CONSTANTS.APP_STATE.ADD_EVENT)
            },
            cancelAdding() {
                // console.log('CURRENT APP state: ' + this.$store.getters.getCurrentState)
                this.$store.dispatch("STATE_CHANGED", CONSTANTS.APP_STATE.CANCEL_ADD_EVENT)
            },
        },
        computed: {
            isAddState() {
                let curState = this.$store.getters.getCurrentState
                return curState == CONSTANTS.APP_STATE.ADD_EVENT || curState == CONSTANTS.APP_STATE.ADD_AND_DRAWN
            },
            isListState() {
                let curState = this.$store.getters.getCurrentState

                return curState == CONSTANTS.APP_STATE.DEFAULT ||
                    curState == CONSTANTS.APP_STATE.CANCEL_ADD_EVENT ||
                    curState == CONSTANTS.APP_STATE.CANCEL_EDIT_EVENT

            },
            drawer: {
                get() {
                    return this.$store.getters.getIsLeftPanelOpen
                },
                set(value) {
                    this.$store.dispatch('SET_IS_LEFT_PANEL_OPEN', value)
                }
            }
        }
    }
</script>

